<template>
  <div>
    <!-- prop -->
    <el-card style="width:500px">
      <el-form :model="userForm" label-width="80px" style="padding-right: 20px;">
        <el-form-item label="" prop="id">
          <el-input v-model="userForm.id" 
          :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name" v-if="user.auth===''">
            <el-input v-model="userForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="tname" v-if="user.auth===''">
            <el-input v-model="userForm.tname" 
            :disabled="true"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="password">
          <el-input v-model="userForm.password"></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="phone">
          <el-input v-model="userForm.phone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="sex">
            <el-input v-model="userForm.sex"></el-input>
        </el-form-item>
        <!-- <el-form-item label="" prop="birth">
          <el-date-picker
                v-model="userForm.birth"
                type="date"
                placeholder=""
                :picker-options="time_option"
                style="width: 200px"
              >
              </el-date-picker>
            <el-input v-model="userForm.birth"></el-input>
        </el-form-item>
        <el-form-item label="" prop="endata">
            <el-input v-model="userForm.endata" disabled="ture"></el-input>
        </el-form-item> -->
        <el-form-item label="" prop="pro" v-if="user.auth===''">
            <el-input v-model="userForm.pro" 
            :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="salary" v-if="user.auth===''">
            <el-input v-model="userForm.salary" 
            :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="classId" v-if="user.auth===''">
            <el-input v-model="userForm.classId" 
            :disabled="true"></el-input>
        </el-form-item>
        <el-form-item label="" prop="home" v-if="user.auth===''">
            <el-input v-model="userForm.home"></el-input>
        </el-form-item>
      </el-form>
      <div style="margin-left: 80%;">
        <el-button @click="update"> </el-button>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  data() {
    return {
      userForm:{
          id:"",
          name:"",
          tname:"",
          password:"",
          phone:"",
          sex:"",
          birth:"",
          endata:"",
          salary:"",
          classId:"",
          pro:"",
          collegeId:"",
          home:"",
      },
      user: JSON.parse(localStorage.getItem('honey-user') || '{}')
    }
  },
  // 
  mounted() {
    if(this.user.auth===''){
    this.$request.get("/student/selectuser",{
      params: {
            username: this.user.username,
          },
    }).then((res) => {
      this.userForm = res.data;
      // this.userForm.password=this.user.password;
    });
  }
  else if(this.user.auth===''){
    this.$request.get("/teacher/selectuser",{
      params: {
            username: this.user.username,
          },
    }).then((res) => {
      this.userForm = res.data;
      // this.userForm.password=this.user.password;
    });
  }
  },
  methods: {
    update() {
      // 
      if(this.user.auth===''){
      this.$request.put('/student/update', this.userForm).then(res => {
        if (res.code === '200') {
          // 
          this.$message.success('')
        } else {
          this.$message.error(res.msg)
        }
      })
    }
  if(this.user.auth===''){
      this.$request.put('/teacher/update1', this.userForm).then(res => {
        if (res.code === '200') {
          // 
          this.$message.success('')
        } else {
          this.$message.error(res.msg)
        }
      })
    }
  },
  }

}
</script>

