<template>
  <div id="poster">
    <el-header>
      <el-row>
        <el-col :span="12"
          ><div class="grid-content bg-purple" style="text-align: left">
            <!-- <router-link to="/Home"> -->
            <strong></strong>
            <!-- </router-link> -->
          </div></el-col
        >
        <el-col :span="12"
          ><div class="grid-content bg-purple-light" style="text-align: right">
            <el-button
              type="primary"
              style="background: #505458; border: none"
              @click="toRegister('ruleForm')"
              ></el-button
            >
            <el-button type="primary" style="background: #505458; border: none"
              ></el-button
            >
          </div></el-col
        >
      </el-row>
    </el-header>
    <el-main>
      <!--  -->
      <el-form
        :model="loginForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="0px"
        class="login-Form"
      >
        <h3 class="login-title"></h3>
        <!--  -->
        <el-form-item label="" prop="username">
          <!--  -->
          <el-input
            type="text"
            v-model="loginForm.username"
            prefix-icon="el-icon-user"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="loginForm.password"
            prefix-icon="el-icon-lock"
            placeholder=""
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item prop="code">
          <div style="display: flex">
            <el-input
              placeholder=""
              prefix-icon="el-icon-circle-check"
              size="medium"
              style="flex: 1"
              v-model="loginForm.code"
              clearable
            ></el-input>
            <div style="flex: 1; height: 36px">
              <!--  -->
              <valid-code @update:value="getCode" />
            </div>
          </div>
        </el-form-item>
        <el-form-item style="text-align: center">
          <!-- @click="onSubmit"@clickv-on: click-->
          <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="login"
            >  </el-button
          >
          <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="resetForm('ruleForm')"
            >  </el-button
          >
          <!-- <el-button type="primary" style="background: #505458;border: none;" @click="toRegister"></el-button> -->
        </el-form-item>
        <div style="display: flex">
          <!-- @click="$router.push('/register')" -->
          <div style="flex: 1">
            
            <span style="color: #7d807f; cursor: pointer" @click="toRegister"
              > </span
            >
          </div>
          <!-- <div style="flex: 1; text-align: right">
            <span style="color: #7d807f; cursor: pointer"></span>
          </div> -->
        </div>
      </el-form>
    </el-main>
  </div>
</template>
<script>
import ValidCode from "@/components/ValidCode.vue";
// import axios from 'vuetest/node_modules/axios/dist/axios'
export default {
  name: "Login",
  components: {
    ValidCode,
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validateCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value.toLowerCase() != this.code) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    return {
      code: "", //code
      loginForm: {
        username: "", //code
        password: "",
        code: "",
      },
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 9, message: "2-9", trigger: "blur" },
        ],
        password: [{ validator: validatePass, trigger: "blur" }],
        code: [{ validator: validateCode, trigger: "blur" }],
      },
    };
  },
  methods: {
    getCode(code) {
      // this.code = code;
      // 
      this.code = code.toLowerCase();
    },
    login() {
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          // 
          this.$request.post("/user/login", this.loginForm).then((res) => {
            console.log(res);
            if (res.code === "200") {
              this.$message({
                message: "!~",
                type: "success",
              });
              localStorage.setItem("honey-user",JSON.stringify(res.data)) //
              // this.$router.push({ path: "/Home" });
                this.$router.push("/Home");
            } else {
              this.$message.error(res.msg);
            }
          });
        }
      });
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    toRegister() {
      this.$message({
        message: "~",
        type: "success",
      });
      this.$router.push({ path: "/Register" });
    },
  },
};
</script>

<style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 50px;
}
#poster {
  /* :topleftrightbottomcenter */
  background-position: center;
  height: 100%;
  width: 100%;
  /*
    
    1.length:
    2.percentage
    3.cover
    4.contain
    5.no-repeat:
    */
  background-size: cover;
  /* 
    1.static
    2.absolute
    3.relative
    4.fixed
    5.inherit position */
  position: fixed;
  /*  */
  overflow: hidden;
}
body {
  /* 
    background-image: url('../assets/background.png'); */
  /*  */
  margin: 0px;
  padding: 0px;
}
.login-Form {
  /*  */
  border-radius: 15px;
  background-clip: padding-box;
  margin: 90px auto;
  width: 400px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  /* :,,*/
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}
.login-title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
/* arouter-link-active */
.router-link-active {
  text-decoration: none;
  color: rgb(6, 6, 6);
}
a {
  text-decoration: none;
  color: rgb(7, 7, 7);
}
</style>
