<template>
  <body id="poster">
    <el-header>
      <el-row>
        <el-col :span="12"
          ><div class="grid-content bg-purple" style="text-align: left">
            <!-- <router-link to="/Home" > -->
            <strong></strong>
            <!-- </router-link> -->
          </div></el-col
        >
        <el-col :span="12"
          ><div class="grid-content bg-purple-light" style="text-align: right">
            <el-button type="primary" style="background: #505458; border: none"
              ></el-button
            >
            <el-button
              type="primary"
              style="background: #505458; border: none"
              @click="$router.push('/Login')"
              ></el-button
            >
          </div></el-col
        >
      </el-row>
    </el-header>
    <el-main>
      <!--
        :model="ruleForm"VueruleFormv-model
        status-icon
        :rules="rules"Vuerules
        ref="ruleForm"Vuethis.$refs.ruleForm
        label-width="100px"100px
        class="demo-ruleForm"
      -->
      <el-form
        :model="RegisterForm"
        status-icon
        :rules="rules"
        ref="ruleForm"
        label-width="0px"
        label-position="left"
        class="register-Form"
      >
        <h3 class="register-title"></h3>

        <div style="display: flex">
            <el-form-item label="" prop="auth" style="flex: 1">
            <el-select
              v-model="RegisterForm.auth"
              placeholder=""
              
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </el-form-item>
        </div>

        <el-form-item label="" prop="username">
          <!--   :prefix-icon ="el-icon-user"-->
          <el-input
            type="text"
            v-model="RegisterForm.username"
            autocomplete="off"
            placeholder=""
            prefix-icon="el-icon-user"
            clearable
          >
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input
            type="password"
            v-model="RegisterForm.password"
            autocomplete="off"
            prefix-icon="el-icon-lock"
            placeholder=""
            clearable
          >
          </el-input>
        </el-form-item>
        <el-form-item label="" prop="checkPass">
          <el-input
            type="password"
            v-model="RegisterForm.checkPass"
            autocomplete="off"
            prefix-icon="el-icon-lock"
            placeholder=""
            clearable
          >
          </el-input>
        </el-form-item>

        <el-form-item prop="code">
          <div style="display: flex">
            <el-input
              placeholder=""
              prefix-icon="el-icon-circle-check"
              size="medium"
              style="flex: 1"
              v-model="RegisterForm.code"
              clearable
            ></el-input>
            <div style="flex: 1; height: 36px">
              <!--  -->
              <valid-code @update:value="getCode" />
            </div>
          </div>
        </el-form-item>

        <el-form-item style="text-align: center">
          <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="register()"
            > </el-button
          >
          <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="resetForm('RegisterForm')"
            > </el-button
          >
          <!-- <el-button
            type="primary"
            style="background: #505458; border: none"
            @click="tologin('ruleForm')"
            ></el-button
          > -->
        </el-form-item>
        <div style="flex: 1">
          
          <span style="color: #7d807f; cursor: pointer" @click="tologin"
            > </span
          >
        </div>
      </el-form>
    </el-main>
    <!---->
    <!-- <SetUserForma ref="SetUserForma"></SetUserForma> -->
  </body>
</template>
<script>
import ValidCode from "@/components/ValidCode.vue";
// import SetUserForma from "../page/popup/SetUserForma.vue";
// import Header from '../components/Header.vue'
export default {
  name: "Register",
  components: {
    ValidCode,
    // SetUserForma,
  },
  data() {
    var validatePass = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else {
        if (this.RegisterForm.checkPass !== "") {
          this.$refs.ruleForm.validateField("checkPass");
        }
        callback();
      }
    };
    var validateCode = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value.toLowerCase() != this.code) {
        callback(new Error(""));
      } else {
        callback();
      }
    };
    var validatePass2 = (rule, value, callback) => {
      if (value === "") {
        callback(new Error(""));
      } else if (value !== this.RegisterForm.password) {
        callback(new Error("!"));
      } else {
        callback();
      }
    };
    return {
      code: "", //code
      RegisterForm: {
        username: "",
        password: "",
        checkPass: "",
        url:"@/assets/.png",
        code: "",
        auth: "",
      },
      options: [
        {
          value: "",
          label: "",
        },
        {
          value: "",
          label: "",
        },
        // {
        //   value: "",
        //   label: "",
        // },
      ],
      value: "",
      rules: {
        username: [
          { required: true, message: "", trigger: "blur" },
          { min: 2, max: 9, message: "2-9", trigger: "blur" },
        ],
        password: [{ validator: validatePass, trigger: "blur" }],
        checkPass: [{ validator: validatePass2, trigger: "blur" }],
        code: [{ validator: validateCode, trigger: "blur" }],
      },
    };
  },
  methods: {
    getCode(code) {
      // this.code = code;
      // 
      this.code = code.toLowerCase();
    },
    register() {
      this.$refs["ruleForm"].validate((valid) => {
        if (valid) {
          // 

          this.$request.post("/user/register", this.RegisterForm).then((res) => {
            console.log(res);
            if (res.code === "200") {
              // this.$router.push({ path: "/Home" });
              // this.$message.success('~')
              // if (this.RegisterForm.auth === "") {
              //   this.$message({
              //   message: "",
              //   type: "success",
              // });
              const { username } = res
              this.$message.success(""+username+"");
              this.RegisterForm= {};
              // this.$router.push("/Login");
              // }
              // if (this.RegisterForm.auth === "") {
              //   this.$message.success("");
              //   this.$refs.SetUserForma.teacherforma = true;
              // } else if (this.RegisterForm.auth === "") {
              //   this.$message.success("");
              //   this.$refs.SetUserForma.studentforma = true;
              // }
            } else {
              this.$message.error(res.msg);
            }
          });
        }
      });
    },
    // 
    resetForm(ruleForm) {
      this.$refs[ruleForm].resetFields();
    },
    tologin() {
      this.$message({
        message: "~",
        type: "success",
      });
      this.$router.push({ path: "/Login" });
    },
  },
};
</script>
  <style scoped>
.el-header {
  background-color: #b3c0d1;
  color: #333;
  text-align: center;
  line-height: 50px;
}
#poster {
  /* :topleftrightbottomcenter */
  background-position: center;
  height: 100%;
  width: 100%;
  /*
          
          1.length:
          2.percentage
          3.cover
          4.contain
          5.no-repeat:
          */
  background-size: cover;
  /* 
          1.static
          2.absolute
          3.relative
          4.fixed
          5.inherit position */
  position: fixed;
  margin: 0px;
  padding: 0px;
}
.register-Form {
  /*  */
  border-radius: 15px;
  background-clip: padding-box;
  margin: 60px auto;
  /* margin-left: 50%; */
  width: 400px;
  padding: 35px 35px 15px 35px;
  background: #fff;
  /* :,,*/
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
  justify-content: center;
  align-items: center;
}
.register-title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
/* arouter-link-active */
.router-link-active {
  text-decoration: none;
  color: rgb(6, 6, 6);
}
a {
  text-decoration: none;
  color: rgb(7, 7, 7);
}
</style>
