<template>
  <div>
    <div class="title">
      <h3></h3>
    </div>

    <div class="title">
      <div style="text-align: left; margin: 20px">
        <el-input
          placeholder=""
          v-model="inputteacher"
          clearable
          style="width: 200px; margin-right: 10px"
        ></el-input>
        <el-input
          placeholder=""
          v-model="inputpro"
          clearable
          style="width: 200px; margin-right: 10px"
        ></el-input>
        <el-button icon="el-icon-search" round @click="load(1)"></el-button>
        <el-button icon="el-icon-search" round @click="reset"></el-button>
      </div>
      <div style="margin: 0px 20px">
        <el-button
          icon="el-icon-circle-plus-outline"
          round
          size="small"
          type="primary"
          plain
          @click="handleadd"
          ></el-button
        >
        <el-button
          icon="el-icon-delete"
          round
          size="small"
          type="danger"
          plain
          @click="delbatch"
          ></el-button
        >
      </div>
      <div>
        <el-table
          :data="tableData"
          style="width: 95%; margin-left: 20px; margin-top: 10px"
          height="350"
          :header-cell-style="{
            backgroundColor: 'aliceblue',
            color: '#666',
          }"
          @selection-change="handleSelectionChange"
        >
          <el-table-column
            type="selection"
            width="55"
            align="center"
          ></el-table-column>
          <el-table-column prop="id" label=""></el-table-column>
          <el-table-column prop="tname" label=""></el-table-column>
          <el-table-column prop="sex" label=""></el-table-column>
          <el-table-column prop="endata" label=""></el-table-column>
          <el-table-column prop="pro" label=""></el-table-column>
          <el-table-column prop="salary" label=""></el-table-column>
          <el-table-column prop="phone" label=""></el-table-column>
          <el-table-column prop="name" label=""></el-table-column>
          <el-table-column
            label=""
            width="200"
            align="center"
            v-slot="scope"
          >
            <el-button
              type="primary"
              size="small"
              @click="handleEdit(scope.row)"
              ></el-button
            >
            <el-button type="danger" size="small" @click="del(scope.row.id)"
              ></el-button
            >
          </el-table-column>
        </el-table>
      </div>
      <div style="margin: 10px 100px">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page="pageNum"
          :page-sizes="[10, 20, 30, 40]"
          :page-size="pageSize"
          layout="total,prev, pager, next"
          :total="total"
        >
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title=""
      :visible.sync="dialogFormVisible"
      style="margin-left: 400px; width: 800px; overflow: hidden"
    >
      <el-form :model="addteacher" class="Dialog">
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="tname"
        >
          <el-input v-model="addteacher.tname" style="width: 200px"></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="sex">
          <!-- <el-input v-model="addteacher.sex" style="width: 200px"></el-input> -->
          <el-radio v-model="addteacher.sex" label=""></el-radio>
          <el-radio v-model="addteacher.sex" label=""></el-radio>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="endata"
        >
          <div class="block">
            <el-date-picker
              v-model="addteacher.endata"
              type="date"
              placeholder=""
              :picker-options="time_option"
              style="width: 200px"
            >
            </el-date-picker>
          </div>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="pro">
          <el-input
            v-model="addteacher.pro"
            autocomplete="off"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="salary">
          <el-input
            v-model="addteacher.salary"
            autocomplete="off"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item label="" :label-width="formLabelWidth" prop="phone">
          <el-input
            v-model="addteacher.phone"
            autocomplete="off"
            style="width: 200px"
          ></el-input>
        </el-form-item>
        <el-form-item
          label=""
          :label-width="formLabelWidth"
          prop="name"
        >
          <!-- <el-input
            v-model="addteacher.name"
            autocomplete="off"
            style="width: 200px"
          ></el-input> -->
          <el-select v-model="addteacher.collegeId" style="width: 200px">
              <!-- :label-:
              :value:idshanghai
              :key:value -->
              <el-option
                v-for="item in options"
                :key="item.id"  
                :label="item.name"
                :value="item.id"
              >
              </el-option>
            </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false"> </el-button>
        <el-button type="primary" @click="save"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
    
    <script>
export default {
  data() {
    return {
      // 
      time_option:{
        disabledDate(time){
          return time.getTime() >Date.now();
        }
      },
      inputteacher: "",
      inputpro: "",
      pageNum: 1, //
      pageSize: 5, //
      total: 0,
      dialogFormVisible: false,
      formLabelWidth: "120px",
      ids: [],
      options:[],
      tableData: [
        {
          id: "",
          tname: "",
          phone: "",
          sex: "",
          endata: "",
          salary: "",
          pro: "",
          collegeId: "",
          name: "",
        },
      ],
      addteacher: {
        id: "",
        tname: "",
        phone: "",
        sex: "",
        endata: "",
        salary: "",
        pro: "",
        collegeId: "",
        name: "",
      },
    };
  },
  // 
  // mounted() {
  // axios.get("http://localhost:9091/Student/selectAll").then(res =>{
  //     // 
  //     console.log(res.data)
  //     this.tableData =res.data.data
  // })
  //   request.get("/teacher").then((res) => {
  //     this.tableData = res.data;
  //   });
  // },
  created() {
    this.load();
    this.getCollege();
  },
  methods: {
    getCollege() {
      //
      this.$request.get("/college/selectALL").then((res)=>{
        if (res.code === "200"){
          this.options=res.data
        }
        else{
          this.$message.error(res.msg)
        }
      })
    },
    del(id) {
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$request.delete("/teacher/delete/" + id).then((res) => {
            if (res.code === "200") {
              this.$message.success("");
              this.load(1);
            } else {
              this.$message.error(res.msg);
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    delbatch() {
      if (!this.ids.length) {
        this.$message.warning("");
        return;
      }
      this.$confirm(", ?", "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      })
        .then(() => {
          this.$request
            .delete("/teacher/delete/batch", {
              data: this.ids,
            })
            .then((res) => {
              if (res.code === "200") {
                this.$message.success("");
                this.load(1);
              } else {
                this.$message.error(res.msg);
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "",
          });
        });
    },
    // 
    handleSelectionChange(rows) {
      // 
      this.ids = rows.map((v) => v.id);
    },
    handleEdit(row) {
      //
      this.addteacher = JSON.parse(JSON.stringify(row));
      this.dialogFormVisible = true;
    },
    handleadd() {
      //
      this.addteacher = {};
      this.dialogFormVisible = true;
    },
    save() {
      // this.$refs["reform"].validate((valid) => {
      //   if (valid) {
      this.$request({
        url: this.addteacher.id ? "/teacher/update" : "/teacher/add",
        method: this.addteacher.id ? "PUT" : "POST",
        data: this.addteacher,
      }).then((res) => {
        if (res.code === "200") {
          this.$message.success("");
          this.load(1);
          this.dialogFormVisible = false;
        } else {
          this.$message.error(res.msg);
        }
        //   });
        // }
      });
    },
    reset() {
      this.inputteacher = "";
      this.inputpro = "";
      this.load();
    },
    load(pageNum) {
      //
      if (pageNum) {
        this.pageNum = pageNum;
      }
      this.$request
        .get("/teacher/selectByPage", {
          params: {
            pageNum: this.pageNum,
            pageSize: this.pageSize,
            tName: this.inputteacher,
            pro: this.inputpro,
          },
        })
        .then((res) => {
          this.tableData = res.data.records;
          this.total = res.data.total;
        });
    },
    handleCurrentChange(pageNum) {
      // this.pageNum = pageNum;
      this.load(pageNum);
    },
  },
};
</script>